import React, { Component } from 'react'
import './posts.css';
export default class Posts extends Component {
    constructor()
    {
        super();
        this.pic = [ '/Images/p1.jpg', '/Images/p2.jpg', '/Images/p3.jpg'];
    }
    render() {
        let left = true;
        return (
            <div className="posts">
                
                {
                    this.pic.map((value,key)=>{
                        console.log(value)
                        return(
                            <div key={key} className="postseg">
                                {left&&<img className="postpic" src={value} data-aos="zoom-in" alt="" />}
                                <div className="desc" data-aos="fade-up">Some catchy captions goes here this is a place holder</div>
                                {left||<img className="postpic" src={value} data-aos="zoom-in" alt="" />}
                                {left = !left}
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}
